import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import InstallIssue from '@/components/InstallIssue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/issue',
      name: 'InstallIssue',
      component: InstallIssue
    }
  ]
})



